.preloader {
  height: 110px;
  width: 110px;
  margin-top: -100px;
  margin-left: -55px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader svg {
  width: 110px;
  height: 110px;
}

.preloader #loaderSun path {
  stroke: #9ea1a4;
  stroke-width: .25px;
  fill: #241e20;
}

.preloader #loaderCloud {
  z-index: 2;
  position: relative;
}

.preloader #loaderCloud path {
  fill: #efefef;
}

.preloader #loaderSun {
  opacity: 0;
  width: 60px;
  height: 60px;
  z-index: 1;
  margin-top: 6px;
  margin-left: -10px;
  animation-name: rotate;
  animation-duration: 16s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 15px;
  left: 45px;
}

.preloader #loaderSun path {
  stroke-width: .18px;
  fill: #9ea1a4;
}

@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }

  100% {
    transform: rotateZ(360deg);
  }
}

.preloader .loaderRain {
  width: 70px;
  height: 70px;
  margin-top: -32px;
  margin-left: 19px;
  position: absolute;
}

.preloader .drop {
  opacity: 1;
  float: left;
  width: 3px;
  height: 10px;
  background: #9ea1a4;
  border-radius: 0 0 6px 6px;
  margin-left: 4px;
  animation-name: drop;
  animation-duration: .35s;
  animation-iteration-count: infinite;
  display: block;
}

.preloader .drop:nth-child(1) {
  animation-delay: -130ms;
}

.preloader .drop:nth-child(2) {
  animation-delay: -240ms;
}

.preloader .drop:nth-child(3) {
  animation-delay: -390ms;
}

.preloader .drop:nth-child(4) {
  animation-delay: -525ms;
}

.preloader .drop:nth-child(5) {
  animation-delay: -640ms;
}

.preloader .drop:nth-child(6) {
  animation-delay: -790ms;
}

.preloader .drop:nth-child(7) {
  animation-delay: -900ms;
}

.preloader .drop:nth-child(8) {
  animation-delay: -1050ms;
}

.preloader .drop:nth-child(9) {
  animation-delay: -1130ms;
}

.preloader .drop:nth-child(10) {
  animation-delay: -1300ms;
}

@keyframes drop {
  50% {
    height: 45px;
    opacity: 0;
  }

  51% {
    opacity: 0;
  }

  100% {
    height: 1px;
    opacity: 0;
  }
}

.preloader .text {
